import { parse } from "query-string"
import PropTypes from "prop-types"
import React, { useState } from "react"
import { graphql, useStaticQuery } from "gatsby"

import LanguageSwitcher from "./LanguageSwitcher"
import LocalizedLink from "./LocalizedLink"
import HeaderBG from "./background"
// import styled from "styled-components"
// import ContextConsumer from "./Context"
import Logo from "./logo"
import { useTranslation } from "react-i18next"
const Header = ({ i18n, siteTitle, originalPath, ...props }) => {
  const { t } = useTranslation()
  const [isOpen, setIsOpen] = useState(false)

  const { desktop } = useStaticQuery(
    graphql`
      query {
        desktop: file(relativePath: { eq: "bg.jpg" }) {
          childImageSharp {
            fluid(quality: 100, maxWidth: 626) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )
  const imageData = desktop.childImageSharp.fluid

  function checkActive(path, val) {
    // console.log("checkActive", path, ", ", val)
    if (path === originalPath) {
      const filter = parse(props.location.search).f
      if (val === "" && filter === undefined) return true
      else return parse(props.location.search).f === val
    }
    return originalPath === path + "/"
  }

  const baseState =
    "flex relative whitespace-no-wrap leading-normal focus:outline-none hover:text-gray-100 text-xl items-center font-nav"
  const normalState = " text-gray-500"
  const activeState = " text-gray-300 underline" //'border-gray-100 text-gray-100 underline'
  const pad = " px-10"

  return (
    <header>
      {/* {console.log("Navbar : ", props)} */}
      <HeaderBG
        imageData={imageData}
        className="fixed z-10 sm:relative flex flex-col justify-between  w-full sm:flex-col sm:justify-between bg-cover bg-center bg-scroll"
      >
        {/* <div className="text-white">{props.pageContext.langKey}</div> */}
        <div className="flex flex-row justify-between relative">
          {/* Logo */}
          <div className="focus:outline-none h-32 sm:h-48">
            <LocalizedLink to="/">
              <Logo className="absolute top-0 h-24 sm:h-40 sm:mt-4 sm:ml-12 fill-current" />
            </LocalizedLink>
          </div>
          {/* Menu stripes */}
          <div className="sm:hidden mt-8 mr-8 textgray-400 fill-current">
            <button
              onClick={() => setIsOpen(!isOpen)}
              type="button"
              className="flex text-blue-200 focus:outline-none text-gray-600 hover:text-gray-100"
            >
              <svg viewBox="0 0 24 24" className=" h-12 w-12 fill-current">
                <path
                  fillRule="evenodd"
                  d="M4 5h16a1 1 0 0 1 0 2H4a1 1 0 1 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2zm0 6h16a1 1 0 0 1 0 2H4a1 1 0 0 1 0-2z"
                ></path>
              </svg>
            </button>
          </div>
        </div>
        {/* Navigation */}
        <nav
          className={
            (isOpen ? "block" : "hidden") +
            " pt-4 pb-4 sm:pb-0 sm:pt-0 sm:block sm:h-16 w-full transparency025 fade-in z-20 bg-green-600"
          }
        >
          <div className="flex flex-col justify-center h-full sm:flex-row sm:justify-center">
            <div
              className={
                checkActive("/", "")
                  ? baseState + activeState + pad
                  : baseState + normalState + pad
              }
              tabIndex={0}
              role="button"
            >
              <LocalizedLink to="/">{t("index.all")}</LocalizedLink>
            </div>
            <div
              className={
                checkActive("/", "3d")
                  ? baseState + activeState + pad
                  : baseState + normalState + pad
              }
              tabIndex={0}
              role="button"
            >
              <LocalizedLink to="/" param="f=3d">
                {t("index.3d")}
              </LocalizedLink>
            </div>
            <div
              className={
                checkActive("/", "art")
                  ? baseState + activeState + pad
                  : baseState + normalState + pad
              }
              tabIndex={0}
              role="button"
            >
              <LocalizedLink to="/" param="f=art">
                {t("index.art")}
              </LocalizedLink>
            </div>
            <div
              className={
                checkActive("/games", "")
                  ? baseState + activeState + pad
                  : baseState + normalState + pad
              }
              tabIndex={0}
              role="button"
            >
              <LocalizedLink to="/games"> {t("games.title")}</LocalizedLink>
            </div>
            <div className="sm:pt-32 sm:absolute sm:top-0 sm:right-0 sm:pr-4 sm:mt-6">
              <LocalizedLink
                to="/contacts"
                className={
                  checkActive("/contacts", "")
                    ? baseState + activeState + " px-10 mr-2 sm:px-0"
                    : baseState + normalState + " px-10 mr-2 sm:px-0"
                }
                tabIndex={0}
                role="button"
              >
                {t("contacts.title")}
              </LocalizedLink>
            </div>
          </div>
          <div className=" text-gray-300 sm:absolute sm:top-0 sm:right-0 font-nav flex flex-row justify-end mr-4 sm:mt-4">
            <LanguageSwitcher originalPath={originalPath} />
          </div>
        </nav>
      </HeaderBG>
      {/* <div>
        <div className="bg-orange-500">Header OP:{originalPath}</div>
      </div> */}
    </header>
  )
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
