import React from "react"
import { Link } from "gatsby"

import FlagRU from "../../images/flag_ru.svg"
import FlagEN from "../../images/flag_en.svg"

import { languages, getLocalizedPath } from "../../i18n"
import { useTranslation } from "react-i18next"
// import SEO from "../seo"
import Helmet from "react-helmet"

const flags = {
  ru: <FlagRU />,
  en: <FlagEN />,
}

const LanguageSwitcher = ({ originalPath }) => {
  const { t, i18n } = useTranslation()
  const locale = i18n.language
  return (
    //font-nav absolute top-0 right-0 mr-4 mt-20 sm:top-0 sm:right-0  sm:mt-2

    <div className=" flex flex-row">
      <Helmet>
        <link
          rel="canonical"
          href={"https://www.alive-life.com" + originalPath}
        />
      </Helmet>
      {/* {console.log("LanguageSwitcher originalPath ", originalPath)}
      {console.log("LanguageSwitcher locale ", locale)}
      {console.log("LanguageSwitcher languages ", languages)} */}
      {/* <div className="px-4">LanguageSwitcher </div> */}
      {/* <div className="px-4">originalPath: {originalPath}</div> */}
      {/* <div className="px-4">locale: {locale}</div> */}
      {languages.map(lang =>
        lang.locale === locale ? (
          <div key={lang.locale} className="flex px-2 h-6">
            {/* <Helmet>
              <link
                rel="alternate"
                hreflang={lang.locale}
                href={
                  "https://www.alive-life.com" +
                  getLocalizedPath(originalPath, lang.locale)
                }
              />
            </Helmet> */}
            {flags[lang.locale]}
          </div>
        ) : (
          // <img key={lang.locale} src={flags[lang.locale]} alt={lang.label} />
          <Link
            className="flex px-2  h-6"
            key={lang.locale}
            to={getLocalizedPath(originalPath, lang.locale)}
          >
            {/* <Helmet>
              <link
                rel="alternate"
                hreflang={lang.locale}
                href={
                  "https://www.alive-life.com" +
                  getLocalizedPath(originalPath, lang.locale)
                }
              />
            </Helmet> */}
            {flags[lang.locale]}
            {/* {<img src={flags[lang.locale]} alt={lang.label} />} */}
          </Link>
        )
      )}
    </div>
  )
}

export default LanguageSwitcher
