const languages = require("../config/languages")
const projects = require("../../data/projects.json")

const defaultLanguage = languages.find(lang => lang.default)

if (!defaultLanguage) {
  throw new Error(
    "Default language has to be specified in the language configuration."
  )
}
// add projects paths
// console.log("Default language:", defaultLanguage)
// console.log("Projects JSON:", projects)

projects.forEach(element => {
  // console.log("Project:", element.project_id)
  const projecti18n = element.i18n
  const defaultProjectPath =
    "/" + element.i18n.find(lang => lang.locale === defaultLanguage.locale).path
  // console.log("defaultProjectPath", defaultProjectPath)

  projecti18n.forEach(elementi18n => {
    // console.log("  i18n locale:", elementi18n.locale)
    // console.log("  i18n slug:", elementi18n.path)

    languages.forEach(lang => {
      if (lang.locale === elementi18n.locale) {
        lang.routes[defaultProjectPath] = "/" + elementi18n.path
      }
    })
    // elementi18n.locale
  })
})
// console.log("Languages routes:", languages)

const getLocalizedPath = (originalPath, locale) => {
  const keyPath = originalPath.replace(/(\w+)\/$/, "$1")
  const lang = languages.find(lang => lang.locale === locale)

  const isDefault = locale === defaultLanguage.locale
  const localizedPath = lang && lang.routes && lang.routes[keyPath]

  if (!localizedPath) {
    throw new Error(
      `You have to specify a translation for all of your routes.\n There is no translation for ${originalPath} in language ${locale}.`
    )
  }
  return isDefault ? localizedPath : `/${locale}${localizedPath}`
}

module.exports = getLocalizedPath
