import React from "react"
import { Link } from "gatsby"

import { getLocalizedPath } from "../../i18n"
import { useTranslation } from "react-i18next"

const LocalizedLink = ({ to, param, ...props }) => {
  const { i18n } = useTranslation()
  const val = param !== undefined ? "?" + param : ""
  return (
    // <div>
    // {/* {console.log("LocalizedLink: ", getLocalizedPath(to, i18n.language))} */}
    // {console.log("LocalizedLink param: ", val)}
    <Link {...props} to={getLocalizedPath(to, i18n.language) + val} />
    // </div>
  )
}
export default LocalizedLink
