import React from "react"
import PropTypes from "prop-types"
// import { useTranslation } from "react-i18next"
// import {  graphql } from "gatsby"
import { withTrans } from "../i18n/withTrans"
// import { withContext } from "../components/withContext"

import Header from "./header"
import Footer from "./footer"

const Layout = ({ children, pageContext, ...props }) => {
  // const { t } = useTranslation()
  // console.log("Layout props: ", props)

  return (
    <>
      <Header originalPath={pageContext.originalPath} {...props} />
      <div>
        {/* <div>This page i18n.language: {i18n.language}</div>
        <div>This page locale: {pageContext.locale}</div>
        <div>This page originalPath: {pageContext.originalPath}</div> */}
        {children}
      </div>
      <Footer />
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

//export default withContext(withTrans(Layout))
export default withTrans(Layout)
