import React, { Component } from "react"
import i18next from "./config/config"
import { I18nextProvider, withTranslation } from "react-i18next"

export function withTrans(WrappedComponent) {
  WrappedComponent = withTranslation()(WrappedComponent)

  return class extends Component {
    render() {
      // console.log("Wrapper: ", i18next)
      // console.log("Wrapper Translation: ", this.props)
      // console.log("pageContext.locale: ", this.props.pageContext.locale)
      i18next.changeLanguage(this.props.pageContext.locale)
      return (
        <I18nextProvider i18n={i18next}>
          <WrappedComponent {...this.props} language={i18next.language} />
        </I18nextProvider>
      )
    }
  }
}
